#balanceRoot {

  .addressContainer {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    flex: 1;
    white-space: nowrap;
    font-size: 0.83rem;
    text-overflow: ellipsis;
    cursor: pointer;
    padding: 28px 30px;
    border-radius: 50px;
    align-items: center;
    max-width: 500px;
    border: 1px solid #ffffff;
    background-color: #2A3040;
    color: #ffffff;
    //border: 1px solid red;
  }
  .balance-container,.overview {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 28px 30px;
    border-radius: 50px;
    //align-items: center;
    margin-top: 40px;
    width: 100%;
    border: 1px solid #ffffff;
    background-color: #2A3040;
    color: #ffffff;
    .balance{
      .roi-container{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .roi{
          display: inline-block;
          font-size: 22px;
          color: #fff;
          line-height: 22px;
          padding: 0;
          &:not(:last-child){
            margin-left: 22px;
          }
        }
      }
      .balanceName{
        margin-bottom: 6px;
      }
      .linkbalance{
        display: inline-block;
        width: 10rem;
        text-align: center;
        padding: 6px 0;
        text-decoration: none;
        cursor: pointer;
        color: #F5B812;
        font-size: 22px;
        font-weight: 400;
        //border: 1px solid #F5B812;
        border-radius: 8px;
        //border-color: rgba(0, 0, 0, 0.8);
        background: #1B2032;
        &:last-child{
          margin-left: 22px;
        }
        &:hover{
          background: #F5B812;
          color: #fff;
        }
      }
    }
    .overview{
      margin-top: 32px;
      flex-direction: row;
      justify-content: space-between;
      border: none;
      border-radius: 0;
      //border-top: 1px solid #ffffff;
      padding: 0 0 0 10px;
    }
  }
  .ea_button_box {
    background: #2A3040;
    border: solid #ffffff 1px;

    .btn_flex,.btn2-flex {
      width: 100%;
      display: flex;
      justify-content: space-between;

      .ea_button_btn {
        margin: 10px 0 0 0;

        .MuiButton-outlinedPrimary {
          padding: 18px 16px;
          margin-bottom: 0;
          border: solid #F5B812 1px;
          background: #F5B812;
        }
      }
    }
    .btn2-flex{
      margin-top: 16px;
    }
  }
  .ea_header_tit{ background: #2A3040; }
  .ea_val_balance{
    h4{
      color: #ffffff;
    }
  }
  .confirm_btn_box{
    .confirm_btn_item{
      border: 1px solid #F5B812;
      color: #ffffff;
      background-color: #F5B812;
    }
  }
}
@media (max-width:760px){
  #balanceRoot{ border: none; width: 90%;
    .MuiTypography-h5{ padding: 10px 0; text-align: center; border: none; }
    .addressContainer{ width: 80%; padding: 10px; margin: 0 auto; background: #2A3040; border: solid #8498AD 1px;
      .MuiTypography-h3{ font-size: 18px; }
    }
    .balance-container{ padding: 16px; margin: 0 auto; margin-top: 20px; background: #2A3040; border: solid #8498AD 1px;
      .balance .balanceName {padding-top: 6px;width: 6rem;}
      .balance > .roi-container >.roi{font-size: 14px;line-height: 16px;white-space: nowrap;&:not(:last-child){margin-left: 6px;} }
      .balance > .linkbalance{width: 6rem;padding: 6px 0;font-size: 14px;font-weight: 400;border-radius: 8px;
        &:last-child {margin-left: 6px;}
      }
      .overview{padding: 0; justify-content: space-around;margin-top: 20px;}
      .MuiTypography-h3{ font-size: 16px; color: #fff;}
      .MuiTypography-h2{ font-size: 18px; }
      .symbol_name{ font-size: 14px; font-weight: normal; font-style: normal; }
    }
    .ea_button_top,.MuiTypography-h3{ font-size: 18px; text-align: center; width: 100%; margin: 0; color: #ffffff; }
    .MuiTypography-h3{ text-align: left;
      .left{ font-size: 14px; }
    }
    .MuiButton-fullWidth{ background: rgba($color: #2F80ED, $alpha: 0.3); color: #fff; margin-bottom: 15px;  }
    .ea_button_box{ background: #2A3040; border: solid #8498AD 1px;
      .btn_flex,.btn2-flex{ width: 100%; display: flex; justify-content: space-between;
        .ea_button_btn{ margin: 10px 0 0 0;
          .MuiButton-outlinedPrimary{ padding: 5px 10px; margin-bottom: 0; border: solid #8498AD 1px;  background-color: #F5B812;}
        }
      }
      .btn2-flex{
        margin-top: 0;
      }
      .ea_val_container{ margin-top: 10px; position: relative; padding-top: 10px;
        .ea_val_balance{ text-align: right; padding: 0; position: absolute; top: -25px;
          h4{ color: #fff; font-size: 14px; }
       }
      }
      .confirm_btn_box{ display: flex; justify-content: space-between;
        .confirm_btn_item{ min-width: 0; padding: 5px 10px; width: auto; border: solid #8498AD 1px; color: #fff;}
      }
    }

  }

}
